import React, { Component } from 'react';
import Image from './Elements/Image';
import Video from './Elements/Video';
import TextReview from './Elements/TextReview';

export default class Media extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //imgHeight: 'https://preview.keenthemes.com/metronic8/demo1/assets/media/stock/600x600/img-33.jpg',
    }
  }

  componentWillMount() {
    
  }

  onErrorImageUpdate = event => event.target.src = "https://cloud.taggbox.com/widget/themes/media/images/no-image.svg"

  render() {
    const { mediaType, postFile, mediaHeight, mediaWidth, socialNetwork, textContent, rating } = this.props
    return <>
      {
        mediaType == 1 ? <TextReview content={textContent} rating={rating} socialNetworkId={socialNetwork} /> :
          (mediaType == 2 || mediaType == 4) ? <Image postFile={postFile} height={mediaHeight} width={mediaWidth} socialNetworkId={socialNetwork} />
            : (mediaType == 3 || mediaType == 5) ? <Video postFile={postFile} height={mediaHeight} width={mediaWidth} socialNetworkId={socialNetwork} /> : null
      }

    </>
  }
}
