import React, { Component } from 'react';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_MEDIA_NAME, GET_NETWORK_ICON } from '../../../../../Services/Utils/utils';

export default class Image extends Component {
  constructor(props) {
    super(props);
    this.state = {
      
    }
  }
  componentWillMount(){
    
  }

  onErrorImageUpdate = event => {
    event.target.src = baseUrl(`media/images/error/image.svg`)
  }

  render() {
    const { postFile, height, width, socialNetworkId } = this.props

    return <div className="position-relative rounded-bottom-sm bg-light overflow-hidden w-100" style={{paddingBottom:'100%'}}>
      <img className="position-absolute object-center-cover" loading="lazy" src={postFile} height={height} width={width} alt={GET_MEDIA_NAME(postFile)} onError={this.onErrorImageUpdate} />

      {socialNetworkId ? <div className="position-absolute p-2 end-0 bottom-0">
        <img className="img-fluid" loading="lazy" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(socialNetworkId)}.svg`)} height={20} width={20} alt={GET_NETWORK_ICON(socialNetworkId)} />
      </div> : null}
    </div>
  }
}
